/* Footer Container */
.footer-container {
  background: linear-gradient(to right, #4CAF50, #ffffff);
  padding: 40px 30px;
  border-top: 3px solid #f5f5f5;
  box-shadow: 0 -4px 6px rgba(0, 0, 0, 0.1);
  text-align: center;
}
/* Footer Text */
.footer-text {
  font-size: 1.1rem; /* Slightly larger font for emphasis */
  font-weight: 500;
  text-transform: uppercase; /* Capitalized text for better emphasis */
  color: #ffffff; /* White color to match the gradient theme */
  letter-spacing: 1px; /* Adds space between letters for a sleek look */
  margin: 0; /* Removes margin for tight control of space */
}

/* Media Query for Mobile (max-width: 768px) */
@media (max-width: 768px) {
  .footer-container {
    padding: 20px 15px; /* Adjust padding for smaller screens */
  }

  .footer-text {
    font-size: 1rem; /* Slightly smaller text for mobile */
  }
}
