/* Global styling for login page */
.login-page {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    background-color: #f4f6f9; /* Light grey background */
    font-family: 'Roboto', sans-serif;
    padding: 20px;
  }
  
  /* Form container styling */
  .login-page .ant-form {
    width: 100%;
    max-width: 380px;
    background-color: #fff;
    padding: 30px;
    border-radius: 12px;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
    border: 1px solid #ddd; /* Light border for form */
  }
  
  /* Header of the login form */
  .login-page h1 {
    text-align: center;
    font-size: 2rem;
    margin-bottom: 20px;
    color: #303f9f; /* Financial blue color */
    font-weight: bold;
  }
  
  /* Label style for form fields */
  .ant-form-item-label {
    font-size: 1rem;
    font-weight: bold;
    color: #5c6bc0; /* Slightly darker shade of blue */
  }
  
  /* Input fields styling */
  .ant-input {
    border-radius: 8px;
    height: 42px;
    padding: 12px;
    font-size: 1rem;
    border: 1px solid #ddd;
    margin-bottom: 15px;
    transition: all 0.3s ease;
  }
  
  .ant-input:focus {
    border-color: #303f9f; /* Change border to blue on focus */
    box-shadow: 0 0 5px rgba(48, 63, 159, 0.3);
  }
  
  /* Button styling for submit */
  .ant-btn {
    width: 100%;
    border-radius: 8px;
    padding: 12px;
    font-size: 1.1rem;
    font-weight: bold;
    background-color: #303f9f; /* Financial blue color */
    color: #fff;
    border: none;
    transition: all 0.3s ease;
  }
  
  .ant-btn:hover {
    background-color: #1e2a8a; /* Darker blue on hover */
  }
  
  /* Link styling for Register page */
  .d-flex {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .d-flex a {
    font-size: 0.9rem;
    color: #303f9f;
    text-decoration: none;
    transition: all 0.3s ease;
  }
  
  .d-flex a:hover {
    color: #1e2a8a; /* Darker blue for hover effect */
    text-decoration: underline;
  }
  
  /* Loading Spinner centered */
  .spinner {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  
  /* Responsive Design for smaller screens */
  @media (max-width: 768px) {
    .login-page .ant-form {
      padding: 20px;
    }
  
    .login-page h1 {
      font-size: 1.6rem;
    }
  }
  
  @media (max-width: 480px) {
    .login-page .ant-form {
      padding: 15px;
    }
  }
  