*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.content{
  height:100vh
}


.register-page{
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

/* transaction page */
.filters{
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px 20px;
  box-shadow: 0 0 3px grey;
}

/* antd icons */
 .anticon {
    font-size: 20px;
    cursor: pointer;
  }

.active-icon{
  color: black;
}

.active-icon{
  color: grey;
}

.switch-icons{
  border: 1px solid rgba(0, 0, 0, 0.704);
  border-radius: 5px;
  padding: 10px 15px;
}
