/* General Styles for Navbar */
.navbar-custom {
  background-color: #ffffff; /* White background */
  padding: 10px 30px; /* Add padding for better spacing */
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1); /* Subtle shadow for depth */
}

/* Navbar Brand (Logo and Name) */
.navbar-brand {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 2rem;
  font-weight: 700;
  color: transparent;
  background-image: linear-gradient(to right, #4CAF50, #ffffff); /* Green to white gradient */
  -webkit-background-clip: text;
  background-clip: text;
  letter-spacing: 2px;
  text-transform: uppercase;
  text-decoration: none;
  margin-right: auto;
  transition: all 0.3s ease;
}

/* Brand Name Styling */
.brand-name {
  font-size: 2rem;
  color: transparent;
  background-image: linear-gradient(to right, #4CAF50, #484444); /* Green to white gradient */
  -webkit-background-clip: text;
  background-clip: text;
}

/* Navbar Links and User Info */
.navbar-nav .nav-item .nav-link {
  color: #000000 !important;
  font-size: 1.2rem;
  font-weight: 500;
  margin-right: 20px;
  text-transform: capitalize;
  transition: color 0.3s ease;
}

/* Navbar Link Hover Effect */
.navbar-nav .nav-item .nav-link:hover {
  color: #f4d03f; /* Golden yellow on hover */
  text-decoration: underline;
}

/* Logout Button */
.btn-logout {
  background-color: #ffffff;
  color: #4CAF50;
  border: 2px solid #4CAF50;
  padding: 8px 20px;
  font-weight: bold;
  border-radius: 8px;
  transition: background-color 0.3s ease, color 0.3s ease;
}

/* Logout Button Hover */
.btn-logout:hover {
  background-color: #4CAF50;
  color: #ffffff;
  border-color: #4CAF50;
}

/* Navbar Toggler Icon */
.navbar-toggler-icon {
  background-color: #000000; /* Black icon for better visibility */
}

/* Media Query for Tablet and Mobile (max-width: 992px) */
@media (max-width: 992px) {
  .navbar-brand {
      font-size: 1.8rem; /* Slightly smaller font size on medium screens */
  }

  .navbar-nav .nav-item .nav-link {
      font-size: 1rem;
  }
}

/* Media Query for Mobile (max-width: 768px) */
@media (max-width: 768px) {
  .navbar-custom {
      padding: 12px 20px; /* Adjust padding for smaller screens */
  }

  .navbar-brand {
      font-size: 1.6rem; /* Adjust font size for smaller screens */
  }

  .navbar-nav .nav-item .nav-link {
      font-size: 1rem;
      margin-right: 15px; /* Less space between links on smaller screens */
  }

  .btn-logout {
      padding: 8px 20px;
  }
}
